<template>
  <b-card :header="$t('Posts')">
    <b-button
      type="button"
      style="background-color: #ef402b;border-color: #C33421;color: white"
      v-on:click="clearObjects(),showLoader=false,$refs.addPostsModel.show()"
      class="mr-1 mb-2 mt-xl-n2 pull-right"
    >{{$t('Add_Posts')}}</b-button>

    <b-table
      :dark="dark"
      :sort-by.sync="sortBy"
      :hover="hover"
      :striped="striped"
      :bordered="bordered"
      :small="small"
      :fixed="fixed"
      responsive="lg"
      :items="gridData"
      :fields="captions"
      :current-page="currentPage"
      :per-page="perPage"
    >
      <template slot="actions" slot-scope="data">
        <a
          v-on:click="clearObjects(),selectObject(data.item), $refs.updatePostsModel.show()"
          v-b-tooltip.hover
          :title="$t('Update')"
          class="icon mr-2 anchor-pointer"
        >
          <i id="editIcon" class="fa font-2x2 fa-pencil"></i>
        </a>
        <a
          v-on:click="clearObjects(),selectObject(data.item), $refs.deletePostsModel.show()"
          v-b-tooltip.hover
          :title="$t('Delete')"
          class="icon anchor-pointer"
        >
          <i class="cui-trash font-2x2 icons mt-1"></i>
        </a>
      </template>
    </b-table>
    <div class="pt-1 pb-4" v-if="gridData.length == 0">
      <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
      <div class="text-center">
        <b-spinner variant="primary" label="Text Centered"></b-spinner>
      </div>
    </div>
    <nav>
      <b-pagination
        class="pull-right"
        :total-rows="totalRows"
        :per-page="perPage"
        v-model="currentPage"
        prev-text="Prev"
        next-text="Next"
        hide-goto-end-buttons
      />
    </nav>

    <b-modal
      no-close-on-backdrop
      ref="deletePostsModel"
      :title="$t('Delete_Posts')"
      class="modal-primary"
    >
      <p>
        {{$t('Are_you_sure_you_want_to_delete_Posts')}}
        <b style="color: #ef402b"></b> ?
      </p>
      <div slot="modal-footer" class="w-100">
        <b-button
          type="button"
          v-on:click="$refs.deletePostsModel.hide(),clearObjects()"
          class="float-right ml-2"
        >
          <i class="fa fa-close"></i>
          {{$t('Cancel')}}
        </b-button>
        <b-button
          type="button"
          style="background-color: #ef402b;border-color: #C33421;color: white"
          v-on:click="deletePosts($refs.deletePostsModel, deletePost), $refs.deletePostsModel.hide()"
          class="float-right ml-2"
          variant="primary"
        >
          <i class="fa fa-dot-circle-o"></i>
          {{$t('Confirm')}}
        </b-button>
      </div>
    </b-modal>

    <b-modal
      no-close-on-backdrop
      ref="addPostsModel"
      size="lg"
      hide-footer
      :title="$t('Add_Posts')"
      class="modal-primary"
    >
       <b-form @submit.stop.prevent="showLoader=true,clearVideoPost(),savePosts($refs.addPostsModel, selectedPost)">
        <b-row>
          <b-col md="6">
            <b-form-group validated :label="$t('Post_Description')">
              <b-form-textarea
                v-model="selectedPost.PostDescription"
                :placeholder="$t('Please_Enter_Article_Description')"
                required
              ></b-form-textarea>
            </b-form-group>
          </b-col>
 
         <b-col md="6">
           <b-form-group validated :label="$t('Picture_Video')">   
            </b-form-group> 
           <!-- <vue-upload-multiple-image
                @upload-success="uploadImageSuccess"
               @before-remove="beforeRemove"
               @edit-image="editImage"
                @data-change="dataChange"
                :data-images="save_images"
                idUpload="myIdUploadSave"
                style="margin: auto;
                        width: 50%;
                        padding: 10px;"
                        
            ></vue-upload-multiple-image> -->
             <b-form-file
                v-model="selectedPost.postDocuments"
                v-show="disableFileDiv"
                :state="Boolean(selectedPost.postDocuments)"
                :placeholder="$t('Choose_a_Picture_Video')"
                :drop-placeholder="$t('Drop_file_here')"
                @change="handleFileUpload($event)"
                
                multiple
              />

         </b-col>
          
        </b-row>  



         

        <!-- <b-row>  

<b-button type="button" class="ml-2" v-if="uploadVideocheck==false" v-on:click="uploadVideocheck=true" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i> {{$t('Upload Videos')}}</b-button>          

          <b-col md="6" v-if="uploadVideocheck" >
            <b-form-group validated :label="$t('Videos')">
              <b-form-file
                v-model="selectedPost.postDocuments"
                v-show="disableFileDiv"
                :state="Boolean(selectedPost.postDocuments)"
                :placeholder="$t('')"
                :drop-placeholder="$t('Drop_file_here')"
                @change="handleFileUpload($event)"
                
                multiple
              /> -->

               <!-- <vue-upload-multiple-image
                @upload-success="uploadImageSuccess"
               @before-remove="beforeRemove"
               @edit-image="editImage"
                @data-change="dataChange"
                :data-images="save_images"
                idUpload="myIdUploadSave"
                style="margin: auto;
                        width: 50%;
                        padding: 10px;"
                        
            ></vue-upload-multiple-image> -->
            <!-- </b-form-group>
          </b-col> 
        </b-row>-->

          <b-row md="6">
            <div v-for="(image, key) in imagePreview" v-bind:key="image">
              <b-col>
                <img
                  class="preview"
                  thumbnil
                  v-bind:ref="'image' +parseInt( key )"
                  v-show="showPreview"
                  fluid
                  alt="Responsive image"
                  rounded="circle"
                  width="100"
                  height="100"
                  border="5"
                  padding="center"
                />
              </b-col>
            </div>
          <!-- </b-row>&nbsp;&nbsp;&nbsp;&nbsp;
          <b-row md="6"> -->
            <div v-for="(video,key) in videoPreview" v-bind:key="video">
              <b-col>
                <Media
                  :kind="'video'"
                  :isMuted="(false)"
                  v-bind:ref="'video' +parseInt( key )"
                  :autoplay="true"
                  :controls="true"
                  :loop="true"
                  :style="{width: '200px'}"
                  @pause="handle()"
                  id="Pre_Video"
                  src="'Pre_Video'"
                ></Media>
              </b-col>
            </div>
           
         

          </b-row>
&nbsp;&nbsp;&nbsp;&nbsp;

   <b-alert
      :show="dismissCountDown"
      dismissible
      variant="warning"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
      <p>Post will save with in {{ dismissCountDown }} seconds...</p>
      <b-progress
        variant="warning"
        :max="dismissSecs"
        :value="dismissCountDown"
        height="4px"
      ></b-progress>
    </b-alert>


         <b-alert
          show
          variant="danger"
          v-show="imagesize"
          dismissible
        >Image size should be below 400 kb.</b-alert>
        <b-alert
          show
          variant="danger"
          v-show="videoszie"
          dismissible
        >Video Size should be below 2MB.</b-alert>

      
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button
            type="submit"
            class="ml-2"
            id="bt_save"
            v-on:click="showAlert()"
            v-if="showLoader==false "
            style="background-color: #ef402b;border-color: #C33421;color: white"
          >
            <i class="fa fa-dot-circle-o"></i>
            {{$t('Save')}}
          </b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button
            type="button"
            class="ml-2"
            v-on:click="reloadpage(),clearObjects(),$refs.addPostsModel.hide()"
            
          >
            <i class="fa fa-close"></i>
            {{$t('Cancel')}}
          </b-button>
        </div>
      </b-form>
    </b-modal>

   
   
   
   
   
   
   
   
   
   
   
   
   
   
   
   
   
   
   












   
   
   
    <b-modal
      no-close-on-backdrop
      ref="updatePostsModel"
      size="lg"
      hide-footer
      :title="$t('Update Posts')"
      class="modal-primary"
    >
       <b-form @submit.stop.prevent="showLoader=true,clearVideoPost(),updatePosts($refs.updatePostsModel, selectedPost)">
        <b-row>
          <b-col md="6">
            <b-form-group validated :label="$t('Post_Description')">
              <b-form-textarea
                v-model="selectedPost.PostDescription"
                :placeholder="$t('Please_Enter_Article_Description')"
                required
              ></b-form-textarea>
            </b-form-group>
          </b-col>
          <b-col md="6">
            <b-form-group validated :label="$t('Picture_Video')">
              <!-- <b-form-file
                v-model="selectedPost.postDocuments"
                :state="Boolean(postImages)"
                 v-show="disableFileDiv"
                :placeholder="(postImages.length>0 ? postImages[0].PostDocument : 'Choose_a_Picture_Video')"  
                :drop-placeholder="$t('Drop_file_here')"
                @change="handleFileUpload($event)"
                multiple
              /> -->

              <b-form-file
                v-model="selectedPost.postDocuments"
                v-show="disableFileDiv"
                :state="Boolean(selectedPost.postDocuments)"
                 :placeholder="(postImages.length>0 ? postImages[0].PostDocument : 'Choose_a_Picture_Video')"
                :drop-placeholder="$t('Drop_file_here')"
                @change="handleFileUpload($event)"
                
                multiple
              />
            </b-form-group>

            <!-- <vue-upload-multiple-image
                @upload-success="uploadImageSuccess"
               @before-remove="beforeRemove"
               @edit-image="editImage"
                @data-change="dataChange"
                :data-images="update_images"
                idUpload="myIdUploadEdit"
                style="margin: auto;
                        width: 50%;
                        padding: 10px;"
            ></vue-upload-multiple-image> -->
          </b-col>
        </b-row>
        <b-row>
          <b-col md="3" v-for="image in postImages" v-bind:key="image.AdvImageURL">
            <div >
                    <b-img  thumbnail :src="ApiEndPoints+'images/'+image.PostDocument"  v-if="!image.PostDocument.includes('.mp4')"   rounded="left" style="height:100px;width:100px;z-index: 10;" ></b-img>
                    

              
                <video autoplay loop class="embed-responsive-item" v-if="image.PostDocument.includes('.mp4')" style="height:100px;width:100px;z-index: 10;" >
                      <source :src="ApiEndPoints+'images/'+image.PostDocument" type="video/mp4">
                  </video>
             
                      
            <button type="button" class="close" aria-label="Close"  style="position: relative;right: 44%; top: 2%;" v-on:click="addDeletePostImgs(image)">
            <span aria-hidden="true">&times;</span>
              </button>
                      <br> <br>
              </div> 

          </b-col>

          </b-row>

          
          <b-row md="6">
            <div v-for="(image, key) in imagePreview" v-bind:key="image">
              <b-col>
                <img
                  class="preview"
                  thumbnil
                  v-bind:ref="'image' +parseInt( key )"
                  v-show="showPreview"
                  fluid
                  alt="Responsive image"
                  rounded="circle"
                  width="100"
                  height="100"
                  border="5"
                  padding="center"
                />
              </b-col>
            </div>
          <!-- </b-row>&nbsp;&nbsp;&nbsp;&nbsp;
          <b-row md="6"> -->
            <div v-for="(video,key) in videoPreview" v-bind:key="video">
              <b-col>
                <Media
                  :kind="'video'"
                  :isMuted="(false)"
                  v-bind:ref="'video' +parseInt( key )"
                  :autoplay="true"
                  :controls="true"
                  :loop="true"
                  :style="{width: '200px'}"
                  @pause="handle()"
                  id="Pre_Video"
                  src="'Pre_Video'"
                ></Media>
              </b-col>
            </div>
           
         

          </b-row>
&nbsp;&nbsp;&nbsp;&nbsp;

   <b-alert
      :show="dismissCountDown"
      dismissible
      variant="warning"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >
      <p>Post will save with in {{ dismissCountDown }} seconds...</p>
      <b-progress
        variant="warning"
        :max="dismissSecs"
        :value="dismissCountDown"
        height="4px"
      ></b-progress>
    </b-alert>


         <b-alert
          show
          variant="danger"
          v-show="imagesize"
          dismissible
        >Image size should be below 400 kb.</b-alert>
        <b-alert
          show
          variant="danger"
          v-show="videoszie"
          dismissible
        >Video Size should be below 2MB.</b-alert>





        
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button
            type="submit"
            class="ml-2"
            id="bt_update"
             v-if="showLoader==false"
            style="background-color: #ef402b;border-color: #C33421;color: white"
           
          >
            <i class="fa fa-dot-circle-o"></i>
            {{$t('Save')}}
          </b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="reloadpage(),clearObjects(),$refs.updatePostsModel.hide()">
            <i class="fa fa-close"></i>
            {{$t('Cancel')}}
          </b-button>
        </div>
      </b-form>
    </b-modal>
  </b-card>
</template>

<script>
import VueNotifications from "vue-notifications";
import * as toutPaieService from "./ToutPaieService.js";
import Media from "@dongido/vue-viaudio";
import VueUploadMultipleImage from 'vue-upload-multiple-image';
import { debug } from "util";

export default {
  props: {
    caption: {
      type: String,
      default: "Managers"
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Media,
    VueUploadMultipleImage
  },

  data: () => {
    return {
      sortBy: "firstName",
      pageOptions: [5, 10, 15],
      currentPage: 1,
      perPage: 10,
      file: null,
      postsList: [],
      images: [1, 2, 3, 4],
      selectedPost: {},
      deletePost: {
        PostID: null
      },
      editPost: {},
      ApiEndPoints:"",
      imagePreview: [],
      showLoader:false,
      videoPreview: [],
      postImages:[],
      postDocumentsImg:[],
      uploadVideocheck:false,
      postDocuments:[],
      imagePost: [],
      dismissSecs: 10,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      save_images:[],
      removePostImgs:[],
      update_images:[],
      imagesize: false,
      videoszie: false,
      disableFileDiv: true,
      showPreview: false,
      disableSaveButton: true
    };
  },

  computed: {
    fields() {
      return [
        {
          key: "PostDescription",
          label: this.translateString("Post_Description"),
          sortable: true
        },
        {
          key: "PostedOn",
          label: this.translateString("Posted_On"),
          sortable: true
        },
        {
          key: "Comments",
          label: this.translateString("Number_Of_Comments"),
          sortable: true
        },
        {
          key: "Likes",
          label: this.translateString("Number_Of_Likes"),
          sortable: true
        },
        {
          key: "actions",
          label: this.translateString("Action"),
          sortable: false,
          thStyle: { width: "80px !important", "min-width": "80px !important" }
        }
      ];
    },

    gridData: function() {
      return this.postsList;
    },

    totalRows: function() {
      return this.getRowCount();
    },

    getLanguage: function() {
      this.language = window.localStorage.getItem("language");
    },


    captions: function() {
      return this.fields;
    }
  },

  methods: {
    translateString: function(colName) {
      return this.$i18n.t(colName);
    },
    checkFileType: function(file) {
      const acceptedImageTypes = ["image/gif", "image/jpeg", "image/png"];
      if (file && acceptedImageTypes.includes(file["type"])) {
        return true;
      }

      return false;
    },

    handle() {
      console.log("Video paused!, playing in 2 sec...");

      setTimeout(() => {
       // this.$refs.video.play();
      }, 2000);
    },


    clearObjects:function(){
      
      this.selectedPost = {};
      this.deletePost = {};
      this.showLoader=false;
      this.imagePreview = [];
      this.dismissCountDown=0;
      this.postDocumentsImg=[];
      this.videoPreview =[];
      this.save_images =[];
      this.uploadVideocheck=false;
      this.update_images=[];
      this.postDocuments=[];
      this.reloadpage();
      // document.getElementById('myIdUploadSave').value = '';
      // document.getElementById('myIdUploadEdit').value = '';
      this.imagePreview.length = 0;
      this.videoPreview.length = 0;

    },
    addDeletePostImgs: function(img) {
       console.log(img);
       this.removePostImgs.push(img);
       var index=null;
          
           for (let i = 0; i < this.postImages.length; i++) {
                  if(this.postImages[i].PostDocumentId==img.PostDocumentId){
                        index= i;  
                  }
            
            }

        if(index!=null){
             for (let i = 0; i < this.postImages.length; i++) {
                if (i === index) {
                  this.postImages.splice(i, 1);
                }
           }
      }
      

     },
     

     deletePostImg: function(doc) {

     console.log("delete image",doc);

     let url="post/delete/documents/"+doc.PostDocumentId;
     
     toutPaieService.saveData(doc, url).then(data => {
       
       
       console.log('Deeleted datadata', data);
         
       let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
         VueNotifications.success({message: this.translateString("Post_Document_Deleted_Successfully"),timeout:2500});
          //window.location.reload();
        }else{
           VueNotifications.error({message:msg,timeout:2500});
        }   
         
      });



    },

    dataURItoBlob(dataURI) {
    var byteString = atob(dataURI.split(',')[1]);
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: 'image/jpeg' });
},

uploadImageSuccess(formData, index, fileList) {
      let arr = [];
      fileList.forEach(element => {
        arr.push(this.dataURItoBlob(element.path));
      });
        console.log('arr',arr);
      this.postDocumentsImg = arr;
      if(this.editMode == true){
        //  console.log('arr',arr);
         
      this.update_images = fileList;
      
      }else{
        //  console.log('arr',arr);
      this.save_images = fileList;
      }
     
     

    },

    reloadpage(){
      if(this.uploadVideocheck){
             this.uploadVideocheck=false;
                location.reload();
           }
    },

     

    beforeRemove (index, done, fileList) {
      //console.log('index', index, fileList);
  
      for (let i = 0; i < this.postDocumentsImg.length; i++) {
        if (i === index) {
          this.postDocumentsImg.splice(i, 1);
        }
      }
      // console.log('index', index, fileList);
      // console.log('this.productsImages',this.productsImages);
  
    
      
      //var r = confirm("remove image")
      //if (r == true) {
        done()
     // } else {
      //}
    },
    editImage (formData, index, fileList) {
      console.log('edit data', formData, index, fileList)
    },
    dataChange (data) {
      console.log("data ",data  )
    },

     countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert() {
        if(this.uploadVideocheck){
        this.videoPreview =[];
        this.imagePreview=[];
        }
        // if(this.uploadVideocheck){
             this.dismissCountDown = this.dismissSecs
          //  }
      },



    handleFileUpload: function(e) {
      //this.filesname = e.target.files[0].name;
      // for (var i = 0; i < selectedFiles.length; i++) {
      //   console.log("FileList", e.target.files[i].name);
      // }
    //postDocuments
     for(let a=0;a<e.target.files.length;a++){
       if(a==0){
           this.selectedPost.postDocuments=e.target.files[a].name;
       }else{
           this.selectedPost.postDocuments=this.selectedPost.postDocuments+","+e.target.files[a].name;
       }
       
       this.postDocuments.push(e.target.files[a]);
     }

      // console.log("FileList targettt :", e.target);
  
      // console.log("file name   aaaa--->",this.selectedPost.postDocuments);

      for (let i = 0; i < e.target.files.length; i++) {
        if (/\.(jpe?g|png|gif)$/i.test(e.target.files[i].name)) {
          let selectedFiles = e.target.files;

          let filesizeImage = 400000;
          let imagefilesize = e.target.files[i].size;
          let imagefilename = e.target.files[i].name;

          if (imagefilename != null && imagefilesize < filesizeImage) {
            
            this.imagePreview.push(e.target.files[i]);
         //   console.log("Image", this.imagePreview);

            for (let i = 0; i < this.imagePreview.length; i++) {
              let reader = new FileReader(); //instantiate a new file reader
              reader.addEventListener(
                "load",
                function() {
                  if(!this.editUser){
                    this.$refs["image" + parseInt(i)][0].src = reader.result;
                  } else{
                      this.$refs["image" + parseInt(i)][1].src = reader.result;
                  }
                  


                  this.showPreview = true;
                }.bind(this),
                false
              ); //add event listener

              reader.readAsDataURL(this.imagePreview[i]);
            }
            //this.disableSaveButton = true;
          } else {
            this.imagesize = true;
            //this.disableSaveButton = false;
            this.selectedPost.File = null;
          }
        } else {

        this.uploadVideocheck=true;

          let selectedFiles = e.target.files;
          let filesizevideo = 209715200;
          let videofilesize = e.target.files[i].size;
          let videofilename = e.target.files[i].name;
          if (videofilename != null && videofilesize < filesizevideo) {
            this.videoPreview.push(e.target.files[i]);

            console.log("video preview", this.videoPreview);
            for (let i = 0; i < this.videoPreview.length; i++) {
              let videoReader = new FileReader(); //instantiate a new file reader
              videoReader.addEventListener(
                "load",
                function() {
                  this.$refs["video" + parseInt(i)][0].src = videoReader.result;
                  //console.log( "checked",this.$refs["video" + parseInt(i)][0].src = reader.result);
                 // this.$refs.video.play();
                  this.showPreview = true;
                }.bind(this),
                false
              ); //add event listener

              videoReader.readAsDataURL(this.videoPreview[i]);
            }

            //this.disableSaveButton = true;
          } else {
            this.videoszie = true;
            //this.disableSaveButton = false;
            this.selectedPost.File = null;
          }
        }
      }
    },

    getRowCount: function() {
      return this.postsList.length;
    },

    selectObject: function(post) {
    //  console.log("Post", post);
      this.selectedPost = post;

       this.postImages =post.postDocuments;

      this.deletePost = post;
      this.deletePost.PostID = post.PostID;
    //  console.log(this.deletePost);
    },

    clearVideoPost: function(){
       
       if(this.uploadVideocheck){
        this.videoPreview=[];
        this.imagePreview=[];
        this.showAlert();
       }
    },

    deletePosts: function(refs,deletePost) {

      let URL = "post/delete/" + deletePost.PostID;

      toutPaieService.getData(URL).then(res => {
        let msg = res.status;
       // console.log("Response", this.msg);
        if (msg == "Success") {
          refs.hide();
          VueNotifications.success({
            message: "Post Deleted Successfully  ",
            timeout: 2500
          });
          
          this.getPosts();
        } else {
          VueNotifications.error({
            message: " Post Not Deleted",
            timeout: 2500
          });
        }
      });
    },

    updatePosts: function(ref,post) {
      this.editUser = true;
      this.savePosts(ref, post);
    },

    savePosts: function(ref, selectedPost) {
  
 
 if(this.uploadVideocheck){

   document.getElementById("bt_save").disabled=true;
   document.getElementById("bt_update").disabled  =true;

    VueNotifications.warn ({
          message: "Please wait, Document File is Uploding...!",
          timeout: 10000
        });
        setTimeout(() => {
          this.videoPreview=[] ;this.videoPreview=[] ;
        }, 0);
 }
      this.uploadVideocheck==true? this.videoPreview=[] :this.videoPreview=[] ;
      this.showAlert();

      let data = {};
      data = selectedPost;
      data.postDocuments=this.postDocumentsImg;

      if(this.editUser === true){
            if(this.removePostImgs.length>0){
                 for(var d=0;d<this.removePostImgs.length;d++){

                   this.deletePostImg(this.removePostImgs[d]);        
                 }
            }
          }
       
      
        for(let s=0;s<this.postDocumentsImg.length;s++){
          this.postDocuments.push(this.postDocumentsImg[s]);
        }
      //  console.log("this.postDocumentscccccc",this.postDocuments);
      this.postDocumentsImg=[];
      let formData = new FormData();
      this.loginUser = JSON.parse(window.localStorage.getItem("loginUser"));
      // data["UserID"] = this.loginUser.UserID;
      formData.append("UserID", this.loginUser.UserID);
      formData.append("PostDescription", data.PostDescription);
      // formData.append("PostDocument", data.File);
      for (let f = 0; f < this.postDocuments.length; f++) {
        formData.append("PostDocument[" + f + "]", this.postDocuments[f]);
      }
     // console.log("this.postDocuments[i]dddddd",this.postDocuments);


      this.postDocuments=[];
      let url = null;
      if (this.editUser === true && data.PostID) {
        //this.checkValidation (ref);
        url = "post/update/" + data.PostID;
        this.editUser = false;
      } else {
        //this.checkValidation (ref);
        url = "post/save";
      }

      toutPaieService.saveData(formData, url).then(res => {
      
     // console.log("res----->",res);
      let msg=res.data[0].message;

      

        if (res.status == 200 && msg=="Success") {

          ref.hide();
           if(this.uploadVideocheck){
             this.uploadVideocheck=false;
             document.getElementById("bt_save").disabled=false;
             document.getElementById("bt_update").disabled=false;
                location.reload();

           }
       
          this.showLoader=false;
          this.clearObjects();
             this.getPosts();
          VueNotifications.success({
            message: this.translateString("Post_Saved_Successfully"),
            timeout: 2500
          });
       
        } else {
          
          VueNotifications.error({ message: msg, timeout: 3500 });
 
        }
      });
 

    },

    checkValidation(modal) {
      if (this.editUser) {
        this.selectedPost = this.editPost;
      } else {
        this.selectedPost = this.selectedPost;
      }
      if (
        !(
          this.selectedStore &&
          this.selectedPost.PostDescription &&
          this.selectedPost.File
        )
      ) {
        VueNotifications.error({
          message: "Please fill all mandatory fields of Post Info Tab.",
          timeout: 2500
        });
        setTimeout(() => {
          this.tabIndex = 0;
          this.edittabIndex = 0;
        }, 0);
      }
    },

    getPosts: function() {
      toutPaieService.getData("posts").then(res => {
        this.postsList = res.data;
        this.postsList.forEach(post => {
          post.Comments = post.PostComments.length;
        });
      });
    }
  },
  beforeMount() {
    this.ApiEndPoints=toutPaieService.ApiEndPoints;
    this.getPosts();
  }
};
</script>
